import React from 'react'
import Link from 'gatsby-link'

const CoursesOffered = () => (
  <div>
    <section className="nkc-banner hidden-print"></section>
    <main className="nkc-container about-nkc clearfix">
      <section className="breadcumb hidden-print">
        <div className="container clearfix">
          <div className="col-lg-full col-md-full col-sm-full">
            <p><Link to="/">Home</Link> &rarr; <Link to="/academics">Academics</Link> &rarr; Courses Offered</p>
          </div>
        </div>
      </section>
      <div className="clearfix">
        <div className="col-lg-xlarge col-md-xlarge col-sm-full col-print-full clearfix">
          <div className="nkc-container__content clearfix">
            <h1 className="nkc-page-title">Courses Offered</h1>
            <p className="nkc-para-intro">Under the aegis of A. H. S. E. C. and Gauhati University, the college offers the priviledge of imparting studies in Arts Faculty both in H. S. and Degree levels.</p>
            <h2>Higher Secondary  Courses</h2>
            <p>The following subjects are offered in Higher Secondary courses in North Kamrup College.</p>
            <div className="responsive-table">
              <table className="nkc-data-table">
                <thead>
                  <tr>
                    <th className="text-center" colSpan="2">Subjects Offered in H. S. Courses</th>
                  </tr>
                </thead>
                <tbody>
                  <tr>
                    <td>English<br />M. I. L. (Assamese, Bodo)<br />Economics<br />Political Science</td>
                    <td>Education<br />Logic and Philosophy<br />Advanced Assamese<br />Environmental Education</td>
                  </tr>
                </tbody>
              </table>
            </div>
            <br />
            <h3>Note #1</h3>
            <ul>
              <li>English and Environmental Education is compulsory.</li>
              <li>A student must select one Modern Indian Language (M. I. L.).</li>
              <li>
                A student must select three / four elective subject from the following list.
                <ul>
                  <li>Economics</li>
                  <li>Political Science</li>
                  <li>History</li>
                  <li>Education</li>
                  <li>Logic and Philosophy</li>
                  <li>Advanced Assamese</li>
                </ul>
              </li>
              <li>Out of the four optional subjects one automatically becomes the fourth subject in which the concerned student scores the least marks</li>
            </ul>
            <p></p>
            <h2>Three Years Degree Courses</h2>
            <p>According to the approval of the meeting of the Academic Council, Gauhati University, North Kamrup College is implementing Semester System in T. D. C. from session 2011-12. So a student seeking admission into T. D. C. has to complete the course in six semester (two semester per year) as formed by Gauhati University.</p>
            <h3>Schedule for Three Year Degree Courses</h3>
            <div className="responsive-table">
              <table className="table-no-border nkc-feature-table">
                <tbody>
                  <tr>
                    <td>First, Third and Fifth Semester</td>
                    <td>1 August - 31 December (Including examinations)</td>
                  </tr>
                  <tr>
                    <td>Second, Fourth and Sixth Semester</td>
                    <td>1 January - 30 June (Including examinations)</td>
                  </tr>
                  <tr>
                    <td>Semester Break</td>
                    <td>1 July - 31 July</td>
                  </tr>
                </tbody>
              </table>
            </div>
            <p>A student seeking admission into the First semester may choose any of the following subjects as Major subject.</p>
            <div className="responsive-table">
              <table className="nkc-data-table">
                <thead>
                  <tr>
                    <th className="text-center" colSpan="2">List A</th>
                  </tr>
                </thead>
                <tbody>
                  <tr>
                    <td>Assamese<br />Economics<br />Education<br />History</td>
                    <td>English<br />Political Science<br />Philosophy<br />Bodo<br /></td>
                  </tr>
                </tbody>
              </table>
            </div>
            <p>A student with Major has to choose any of the following as First subsidiary subject from the following list.</p>
            <div className="responsive-table">
              <table className="nkc-data-table">
                <thead>
                  <tr>
                    <th className="text-center" colSpan="2">List B</th>
                  </tr>
                </thead>
                <tbody>
                  <tr>
                    <td>Philosophy<br />Education<br />Assamese<br />Political Sciene</td>
                    <td>History<br />Elective<br />Economics<br />Bodo<br /></td>
                  </tr>
                </tbody>
              </table>
            </div>
            <br />
            <p>In addition to it, he / she has to choose M. I. L. (Assamese, Bodo) or Alternative English as Second subsidiary and English as compulsory subject for both semester 1 and 2. In semester 3 and 4, instead of English, he / she has to take Environmental Studies as compulsory subject. In semester 5 and 6, he / she has to continue Major subject only.</p>
            <p>A student without Major has to choose any two subjects from List A as First elective and Second elective and M. I. L. (Assamese, Bodo) as Third elective including English as compulsory subject for the semester 1 and 2. In semester 3 and 4, instead of English, he / she has to take Environmental Studies as compulsory subject. In semester 5 and 6, he / she has to continue First and Second elective subjects only.</p>
          </div>
        </div>
      </div>
    </main>
    <div className="spacer"></div>
  </div>
)

export default CoursesOffered



